"use client"

import { Activity, Moon, Shield, Sun } from "lucide-react"
import { useTheme } from "next-themes"
import { useEffect, useState } from "react"

interface DashboardHeaderProps {
  walletAddress: string | null
  activeView: "overview" | "transactions" | "missions" | "history"
  onViewChange: (view: "overview" | "transactions" | "missions" | "history") => void
  onDisconnect: () => void
}

export function DashboardHeader({ walletAddress, activeView, onViewChange, onDisconnect }: DashboardHeaderProps) {
  const { setTheme, resolvedTheme } = useTheme()
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
  }, [])
  const views = [
    { id: "overview", label: "Overview" },
    { id: "transactions", label: "Transactions" },
    { id: "missions", label: "Missions" },
    { id: "history", label: "History" },
  ] as const

  return (
    <div className="space-y-8">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <div className="h-16 w-16 rounded-2xl bg-gradient-to-br from-primary to-accent flex items-center justify-center shadow-lg shadow-primary/20">
            <Shield className="h-8 w-8 text-white" />
          </div>
          <div>
            <h1 className="text-4xl font-bold text-foreground bg-gradient-to-r from-foreground to-foreground/70 bg-clip-text">
              ARC Score Analytics
            </h1>
            <p className="text-muted-foreground mt-1 text-base">
              Track your on-chain activity and reputation on Arc Network
            </p>
          </div>
        </div>

        <div className="flex items-center gap-3">
          {mounted && (
            <button
              onClick={() => setTheme(resolvedTheme === "dark" ? "light" : "dark")}
              className="hidden lg:inline-flex h-10 w-10 items-center justify-center rounded-lg border border-border hover:border-primary/50 transition-colors"
              aria-label="Toggle theme"
            >
              {resolvedTheme === "dark" ? (
                <Sun className="h-5 w-5 text-primary" />
              ) : (
                <Moon className="h-5 w-5 text-primary" />
              )}
            </button>
          )}

          {walletAddress && (
            <div className="hidden lg:flex items-center gap-3 bg-card/50 backdrop-blur-sm border border-border rounded-xl px-5 py-3">
              <Activity className="h-4 w-4 text-primary" />
              <div className="text-sm">
                <div className="font-mono text-xs text-muted-foreground">Connected</div>
                <div className="font-medium text-foreground">
                  {walletAddress.slice(0, 6)}...{walletAddress.slice(-4)}
                </div>
              </div>
              <button
                onClick={onDisconnect}
                className="text-xs font-semibold px-3 py-1.5 rounded-lg border border-border hover:border-primary/50 hover:text-primary transition-colors"
              >
                Disconnect
              </button>
            </div>
          )}
        </div>
      </div>

      {walletAddress && (
        <nav className="flex gap-2 bg-card/30 backdrop-blur-sm border border-border rounded-xl p-1.5">
          {views.map((view) => (
            <button
              key={view.id}
              onClick={() => onViewChange(view.id)}
              className={`px-6 py-3 text-sm font-semibold transition-all rounded-lg ${
                activeView === view.id
                  ? "bg-primary text-primary-foreground shadow-lg shadow-primary/20"
                  : "text-muted-foreground hover:text-foreground hover:bg-card/50"
              }`}
            >
              {view.label}
            </button>
          ))}
        </nav>
      )}
    </div>
  )
}
